import * as React from 'react';
import { environment, Controller, RenderEditableProps } from '@ali/4ever-cangjie';
import NewlineGuide from './components/newlineGuide';
import { BiPluginConfig } from '../../types';

export default function createRenderEditable(config: BiPluginConfig) {
  return (props: RenderEditableProps, controller: Controller, next) => {
    // 移动端、配置未开启不显示
    const isNecessary = !environment.IS_MOBILE && !!config.newlineGuide?.enabled;

    return (
      <>
        {next()}
        {isNecessary && <NewlineGuide placeholder={props.placeholder!} zoom={props.zoom} controller={controller} {...config.newlineGuide} />}
      </>
    );
  };
}
